.f-bd {
    position: relative
}

.f-bd:after {
    position: absolute;
    z-index: 2;
    content: "";
    top: 0;
    left: 0;
    pointer-events: none;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    transform-origin: top left;
    border: 0 solid rgba(51,51,51,.15)
}

.f-bd.f-bd-light:after {
    border-color: hsla(0,0%,100%,.15)
}

.f-bd.f-bd-btm:after {
    border-width: 0 0 1px
}

.f-bd.f-bd-top:after {
    border-width: 1px 0 0
}

.f-bd2:after {
    position: absolute;
    z-index: 10;
    content: "";
    top: 0;
    left: 0;
    width: 200%;
    height: 200%;
    pointer-events: none;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
    transform: scale(.5);
    transform-origin: top left
}

